import React, { Component } from "react";
import "./input.sass";

class Input extends Component {
  state = {
    listItem: {
      body: ''
    },
    display: false
  };

  handleChange = (e) => {
    const listItem = {
      body: e.target.value,
    }
    this.setState({ listItem });
  }

  handleSubmit = (e) => {
    e.preventDefault();
    const display = !this.state.display;
    this.setState({ display });
    if (this.state.listItem.body === '') return;
    
    const uniqueId = new Date().valueOf();
    const listItem = {
      body: this.state.listItem.body,
      id: uniqueId
    }

    this.props.onAddNote(listItem);
    this.setState({ listItem: {body: ''} });
  }

  render() {
    return <form onSubmit={this.handleSubmit}>
        <input placeholder="add new note" autoFocus={this.state.display} className={this.state.display ? 'showInput' : ''} onChange={this.handleChange} value={this.state.listItem.body} type="text" />
        <button className={this.state.display ? 'submit' : ''}>
          <i className="fa fa-plus-circle" aria-hidden="true" />
        </button>
      </form>;
  }
}

export default Input;



