import React from "react";
import "./header.sass";

const Header = ({onDeleteAll, name}) => {
  return ( 
    <header>
      <img src="./images/logo.png" alt="logo" />
      <h4>{name}</h4>
      <div onClick={onDeleteAll}>
        <i className="fa fa-trash" aria-hidden="true" />
        <p>clear all</p>
      </div>
    </header>
   );
}
 
export default Header;




