import React, { Component } from "react";
import Header from "./common/header";
import Input from "./common/input";
import List from "./common/list";

class Work extends Component {
  state = {
    name: 'work list',
    workList: []
  };

  componentDidMount() {
    if (localStorage.getItem('workList')) {
      const workList = JSON.parse(localStorage.getItem("workList"));
      this.setState({ workList });
    } else {
      const defaultInfo = [
        {body: 'Thank you for using "Simply To Do List" app', id: 1},
        {body: 'There are three different panels on the bottom for you to switch/manage, you are on work list panel now', id: 4},
        {body: 'Click/press the plus sign icon to begin to add your note', id: 5},
        {body: 'These list data store on your local browser', id: 6},
        {body: 'Click/press trash icon on the right to remove this note', id: 2},
        {body: 'Click/press trash icon on the top-right corner to remove all note in this panel', id: 3},
      ];
      localStorage.setItem("workList", JSON.stringify(defaultInfo));
      this.setState({ workList: defaultInfo });
    }
  }

  handleDelete = (id) =>{
    const workList = [...this.state.workList].filter(work => work.id !== id);
    
    localStorage.setItem("workList", JSON.stringify(workList));
    
    this.setState({ workList });
  }

  handleDeleteAll = () => {
    localStorage.setItem("workList", "[]");
    this.setState({ workList: [] });
  }

  handleAddNote = (newListItem) => {
    const workList = [...this.state.workList, newListItem];

    localStorage.setItem("workList", JSON.stringify(workList));

    this.setState({ workList });
  }

  render() {
    return (
      <React.Fragment>
        <Header onDeleteAll={this.handleDeleteAll} name={this.state.name} />
        <List listItems={this.state.workList} onDelete={this.handleDelete} />
        <Input onAddNote={this.handleAddNote} />
      </React.Fragment>
    );
  }
}

export default Work;



