import React, { Component } from "react";
import Header from "./common/header";
import Input from "./common/input";
import List from "./common/list";

class Shopping extends Component {
  state = {
    name: 'shopping list',
    shoppingList: []
  };

  componentDidMount() {
    if (localStorage.getItem('shoppingList')) {
      const shoppingList = JSON.parse(localStorage.getItem("shoppingList"));
      this.setState({ shoppingList });
    } else {
      localStorage.setItem("shoppingList", "[]");
      this.setState({ shoppingList: [] });
    }
  }

  handleDelete = (id) => {
    const shoppingList = [...this.state.shoppingList].filter(shopping => shopping.id !== id);

    localStorage.setItem("shoppingList", JSON.stringify(shoppingList));

    this.setState({ shoppingList });
  }

  handleDeleteAll = () => {
    localStorage.setItem("shoppingList", "[]");
    this.setState({ shoppingList: [] });
  }

  handleAddNote = (newListItem) => {
    const shoppingList = [...this.state.shoppingList, newListItem];

    localStorage.setItem("shoppingList", JSON.stringify(shoppingList));

    this.setState({ shoppingList });
  }

  render() {
    return (
      <React.Fragment>
        <Header onDeleteAll={this.handleDeleteAll} name={this.state.name} />
        <List listItems={this.state.shoppingList} onDelete={this.handleDelete} />
        <Input onAddNote={this.handleAddNote} />
      </React.Fragment>
    );
  }
}

export default Shopping;



