import React from "react";
import "./list.sass";


const List = ({listItems, onDelete}) => {
  return ( 
    <div className="list">
      {listItems.map(item => 
      <div className="list-item" key={item.id || item.body}>
        <p>{item.body}</p>
        <i onClick={() => onDelete(item.id)} className="fa fa-trash" aria-hidden="true" />
      </div>)}
    </div>
   );
}
 
export default List;




