import React, { Component } from "react";
import Header from "./common/header";
import Input from "./common/input";
import List from "./common/list";

class Others extends Component {
  state = {
    name: 'other',
    otherList: []
  };

  componentDidMount() {
    if (localStorage.getItem('otherList')) {
      const otherList = JSON.parse(localStorage.getItem("otherList"));
      this.setState({ otherList });
    } else {
      localStorage.setItem("otherList", '[]');
      this.setState({ otherList: [] });
    }
  }

  handleDelete = (id) => {
    const otherList = [...this.state.otherList].filter(other => other.id !== id);

    localStorage.setItem("otherList", JSON.stringify(otherList));

    this.setState({ otherList });
  }

  handleDeleteAll = () => {
    localStorage.setItem("otherList", "[]");
    this.setState({ otherList: [] });
  }

  handleAddNote = (newListItem) => {
    const otherList = [...this.state.otherList, newListItem];

    localStorage.setItem("otherList", JSON.stringify(otherList));

    this.setState({ otherList });
  }

  render() {
    return (
      <React.Fragment>
        <Header onDeleteAll={this.handleDeleteAll} name={this.state.name} />
        <List listItems={this.state.otherList} onDelete={this.handleDelete} />
        <Input onAddNote={this.handleAddNote} />
      </React.Fragment>
    );
  }
}

export default Others;



